import { getCookie, getLocationVars } from '@/utils/utils'
import { getRatio } from './urils'

/** w2a */
export const checkAdParams = ({
  mediaType,
  callback,
  success
}: {
  mediaType: 'fb' | 'tt' | 'sc'
  callback: (res: any) => void
  /** cookie */
  success: () => void
}): void => {
  let time: NodeJS.Timeout
  // 
  if (mediaType === 'fb') {
    // fbclid
    const { fbclid = '' } = getLocationVars()
    // fbclidcookie_fbcfbclid
    const checkFBC = () =>
      !fbclid || (getCookie('_fbc') || '').split('.').reverse()?.[0] === fbclid
    // fbp
    if (!getCookie('_fbp') || !checkFBC()) {
      time = setInterval(() => {
        // fbcfbpfbcfbp
        if (getCookie('_fbp') && checkFBC()) {
          clearInterval(time)
          callback({
            _fbc: getCookie('_fbc'),
            _fbp: getCookie('_fbp')
          })
        }
      }, 50)
      return
    }
  }
  // snapchatsc_cookie1
  if (mediaType === 'sc') {
    if (!getCookie('_scid')) {
      console.log(
        '%c [ snapchat ]-17',
        'font-size:13px; background:pink; color:#bf2c9f;'
      )
      time = setInterval(() => {
        if (getCookie('_scid')) {
          clearInterval(time)
          callback({
            sc_cookie1: getCookie('_scid')
          })
        }
      }, 50)
      return
    }
  }
  // ttonelinkgoBacksend
  // else if (mediaType === 'tt') {
  //   if (!getCookie('_ttp')) {
  //     console.log(
  //       '%c [ TT ]-17',
  //       'font-size:13px; background:pink; color:#bf2c9f;'
  //     )
  //     time = setInterval(() => {
  //       if (getCookie('_ttp')) {
  //         clearInterval(time)
  //         callback({
  //           ttp: getCookie('_ttp')
  //         })
  //       }
  //     }, 50)
  //     return
  //   }
  // }
  // 
  success()
}

/**  */
export const getUserAgentInfo = (): Promise<null | {
  platform: string
  model: string
  platformVersion: string
}> => {
  return new Promise((resolve, reject) => {
    // @ts-ignore
    if (typeof navigator?.userAgentData?.getHighEntropyValues === 'undefined') {
      resolve(null)
    }
    // @ts-ignore
    navigator.userAgentData
      .getHighEntropyValues([
        // 'architecture',
        // 'bitness',
        'model',
        'platformVersion',
        'fullVersionList'
      ])
      .then((ua: any) => {
        resolve({
          // architecture: ua.architecture,
          // bitness: ua.bitness,
          platform: ua.platform,
          model: ua.model,
          platformVersion: ua.platformVersion?.split('.')?.[0] || ''
          // fullVersionList: ua.fullVersionList,
        })
      })
      .catch((error: any) => {
        resolve(null)
      })
  })
}

/** */
export const getDeviceInfo = async () => {
  const UAData = await await getUserAgentInfo()
  let ua = navigator.userAgent
  console.log(
    '%c [ UAData?.platform ]-100',
    'font-size:13px; background:pink; color:#bf2c9f;',
    UAData?.platformVersion,
    UAData?.platform
  )
  if (UAData && UAData?.platformVersion && UAData?.platform) {
    ua = ua.replace(
      /Android\s+\d+;/,
      `${UAData?.platform} ${UAData?.platformVersion};`
    )
  }

  const h = Math.max(
    Math.floor((window.screen.width * getRatio()) / 100),
    Math.floor((window.screen.height * getRatio()) / 100)
  )
  const w = Math.min(
    Math.floor((window.screen.width * getRatio()) / 100),
    Math.floor((window.screen.height * getRatio()) / 100)
  )

  return {
    ua,
    did: {
      w,
      h
    }
  }
}
