/* eslint-disable @typescript-eslint/ban-ts-comment */
import React, { useEffect, useRef } from 'react'
import { useRequest } from 'ahooks'
import { Toast } from 'antd-mobile'
import * as Sentry from '@sentry/react'
import { getRatio } from './utils/index'
import {
  getLocationVars,
  converUrlParamsObj,
  getRandomStr,
  getUA,
  getCookie
} from '@/utils/utils'
import { reportAdInfo, getIp } from '@/projects/reelshort/services/webtoapp'
import './App.scss'
import app_store from './assets/app_store.png'
import logo from './assets/logo.png'
import google_play from './assets/google_play.png'
import { reportSDK } from '@reelshort/utils/reportInfo/index'
import { getDomainConfig } from './utils'
import { generateW2AOneLink } from '@reelshort/utils/url'
import { checkAdParams, getDeviceInfo } from '../../utils/w2a'
import { getTimeZoneAndDomain } from '../../utils/urils'
const App: React.FC = () => {
  // book_id-id chapterId-id
  const {
    book_id,
    chapterId,
    chapterIndex,
    campaign_id,
    ad_id,
    adset_id,
    mediaType = 'fb',
    ttclid = '',
    gclid = '',
    gbraid = '',
    wbraid = '',
    book_type = '1',
    ad_type = '',
    push_type = '2',
    ScCid = '', // snap
    twclid = '', // twitter
    epik = '' // pinterest
  }: any = getLocationVars()
  /** order_id */
  const _chap_order_id = chapterIndex ? Number(chapterIndex) : ''
  /**  1-app ->onelink type=1000  2-app ->onelink type=1001 */
  const pushType = Number(push_type)
  /**  */
  const domainConfig = useRef<any>({ pixel: '' })
  const { ios, android } = getUA()
  /** UID */
  const initialUid = getRandomStr(12)
  const uidNum =
    localStorage?.getItem('uidNum') || initialUid + new Date().getTime()
  localStorage.setItem('uidNum', `${uidNum}`)
  // 
  const urlQueryParams = {
    bookId: book_id || '',
    //  - 0 |  - id
    chapterId: chapterId || '',
    uid: uidNum,
    scheme: 'cmsvictor',
    // appH5schemeURL
    isFromH5: 2,
    // 1-app2-app
    pushType,
    // 1- 2-
    book_type: Number(book_type),
    // 
    ad_type
  }

  /**  */
  const modifyReport = (data: any, params: any) => {
    reportAdInfo({
      ...data,
      repair: 1,
      arg: {
        ...data.arg,
        ...params
      }
    })
      .then((res) => {
        if (res.data?.forbid > 0) return
        // 0
        if (window.countdownRef !== 0) {
          location.href = generateW2AOneLink({ urlQueryParams, mediaType })
        }
      })
      .catch(() => {
        setTimeout(() => {
          if (window.reportRetry < 4) {
            modifyReport(data, params)
            window.reportRetry++
          }
        }, 1000)
        // 0
        if (window.countdownRef !== 0) {
          console.log('4')
          Sentry.captureMessage(`4`, {
            contexts: {
              local: getTimeZoneAndDomain()
            }
          })
          window.countdownRef = 0
          location.href = generateW2AOneLink({ urlQueryParams, mediaType })
        }
      })
  }

  const { run: getIpHandle, cancel } = useRequest((params) => getIp(), {
    manual: true,
    pollingInterval: 3000,
    onSuccess: async (res, params) => {
      const { time, lastIp } = params[0]
      const deviceInfo = await getDeviceInfo()
      if (res.code === 0) {
        const ip = res.data.ip
        if (time < new Date().getTime()) {
          // 
          cancel()
        }
        if (ip !== lastIp) {
          // 
          cancel()
          const { pixel } = getDomainConfig()
          reportHandl({
            visitorId: uidNum,
            href: location.href,
            referrer: document?.referrer,
            goto: JSON.stringify(urlQueryParams),
            arg: {
              media_type: mediaType,
              pixelid: pixel,
              // fb
              _fbc: getCookie('_fbc'),
              _fbp: getCookie('_fbp'),
              // tt
              ttclid,
              ttp: getCookie('_ttp'),
              // gg
              gclid,
              gbraid,
              wbraid,
              // snap
              sc_click_id: ScCid,
              sc_cookie1: getCookie('_scid'),
              // twitter
              twclid,
              // pinterest
              epik
            },
            lastIp: lastIp,
            ...deviceInfo
          })
        }
      }
    }
  })
  // 
  const { run: reportHandl } = useRequest(
    (data) => {
      checkAdParams({
        mediaType,
        success: () => {
          window.countdownRef = 0
        },
        callback: (params: any) => {
          // send
          window.notAllowW2AJump = true
          // 
          window.reportRetry = 0
          modifyReport(data, params)
        }
      })
      return reportAdInfo(data)
    },
    {
      manual: true,
      onSuccess: (res) => {
        if (res.data?.forbid > 0) {
          Sentry.captureMessage(`IP `, {
            contexts: {
              res: res.data,
              local: getTimeZoneAndDomain()
            }
          })
          return
        }
        if (res.code === 0) {
          cancel()
          const time = new Date().getTime() + 60 * 60 * 1000
          getIpHandle({
            lastIp: res.data.ip,
            time
          })
        }

        const onelink = generateW2AOneLink({ urlQueryParams, mediaType })
        // 
        // 
        reportSDK.clickReport({
          eventName: 'h5_w2a_link_stat',
          properties: {
            _action: 'redirect',
            _story_id: urlQueryParams.bookId,
            _chap_id: urlQueryParams.chapterId || '',
            _chap_order_id: _chap_order_id,
            h5_uid: uidNum,
            _url: location.href,
            pixel_id: domainConfig.current.pixel,
            campaign_id: campaign_id,
            adset_id: adset_id,
            ad_id: ad_id,
            ui_type: 1,
            af_onelink: onelink
          }
        })
        // _fbp_fbc, sc_cookie1
        // 
        if (window.countdownRef - Date.now() > 0) {
          setTimeout(() => {
            // 
            if (!window.notAllowW2AJump) {
              // 
              window.countdownRef = 0
              window.location.href = onelink
            }
          }, window.countdownRef - Date.now())
        } else {
          // 
          window.countdownRef = 0
          window.location.href = onelink
        }
      }
    }
  )

  // app | 
  const handleEvokeApp = async (pixel: string) => {
    const deviceInfo = await getDeviceInfo()

    // 
    const u = navigator.userAgent
    if (/MicroMessenger/gi.test(u)) {
      // 
      Toast.info('')

      return
    } else {
      // onelink
      window.countdownRef = Date.now() + 600
      reportHandl({
        visitorId: uidNum,
        href: location.href,
        referrer: document?.referrer,
        goto: JSON.stringify(urlQueryParams),
        arg: {
          media_type: mediaType,
          pixelid: pixel,
          // fb
          _fbc: getCookie('_fbc'),
          _fbp: getCookie('_fbp'),
          // tt
          ttclid,
          ttp: getCookie('_ttp'),
          // gg
          gclid,
          gbraid,
          wbraid,
          // snap
          sc_click_id: ScCid,
          sc_cookie1: getCookie('_scid'),
          // twitter
          twclid,
          // pinterest
          epik
        },
        ...deviceInfo,
        ...getTimeZoneAndDomain()
      })
    }
  }

  useEffect(() => {
    // pixel
    const { pixel } = getDomainConfig()
    domainConfig.current.pixel = pixel
    if (mediaType === 'fb') {
      // FB
      // @ts-ignore
      fbq('init', pixel)
      // @ts-ignore
      fbq('track', 'PageView')
      document!.querySelector(
        'noscript'
      )!.innerHTML = `https://www.facebook.com/tr?id=${pixel}&ev=PageView&noscript=1`
    } else if (mediaType === 'tt') {
      // @ts-ignore
      ttq.load(pixel)
      // @ts-ignore
      ttq.page()
    } else if (mediaType === 'sc') {
      // @ts-ignore
      snaptr('init', pixel, {
        user_email: '__INSERT_USER_EMAIL__'
      })
      // @ts-ignore
      snaptr('track', 'PAGE_VIEW')
    } else if (mediaType === 'tw') {
      // @ts-ignore
      twq('config', pixel)
    }
    reportSDK.clickReport({
      eventName: 'h5_w2a_link_stat',
      properties: {
        _action: 'show',
        _story_id: urlQueryParams.bookId,
        _chap_id: urlQueryParams.chapterId || '',
        _chap_order_id: _chap_order_id,
        h5_uid: uidNum,
        _url: location.href,
        pixel_id: pixel,
        campaign_id: campaign_id,
        adset_id: adset_id,
        ad_id: ad_id,
        ui_type: 1
      }
    })
    handleEvokeApp(pixel)
  }, [])

  // if (!urlQueryParams.bookId)
  // return <div className='error-tip'>Missing required parameters</div>

  return (
    <div className='page-ctr'>
      {/* <div className='logo_box'>
        <img src={logo} />
        <p>ReelShort</p>
      </div>

      <div className='store'>
        <img src={ios ? app_store : google_play} />
      </div> */}
    </div>
  )
}

export default App
