/**
 * request 
 *  api : https://github.com/umijs/umi-request
 */
import { extend } from 'umi-request'
import { Toast } from 'antd-mobile'
import { getEnv, isDevelopment } from '@/utils/utils'
/**
 * 
 */

const errorHandler = (error: any) => {
  const { response } = error
  console.log(response, 'response', error)

  // if (response && response?.status === 401) {

  // }

  if (
    (error?.data !== undefined && error?.response !== null) ||
    error.toString().indexOf('timeout')
  ) {
    // console.log('')
    Toast.info(
      'Network connection error. Please check your network settings and try again.'
    )
    localStorage.removeItem('cToken')
  }
  throw error
}

const request = extend({
  // 
  errorHandler,
  useCache: true,
  timeout: 20000
  // credentials: 'include', // cookie
})

request.interceptors.request.use(
  (url: string, options: any) => {
    console.log('', getEnv(), url, isDevelopment)
    // /api/v/vV
    const _url = !isDevelopment ? url.replace('/api', '/v') : url
    const headers: any = {
      uid: localStorage.getItem('uid') || ''
    }
    return {
      url: _url,
      options: {
        ...options,
        headers
      }
    }
  },
  { global: false }
)

request.interceptors.response.use(
  async (response, req) => {
    if (req.responseType === 'blob') {
      return response
    }

    const res = await response.clone().json()

    return res
  },
  { global: false }
)

export default request
